@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Major Mono Display', monospace;
  line-height: 1.5;
}

.app {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #151618;
  color: #2c888d;
}

h2 {
  color: #49b2b8;
  font-size: 30px;
  text-align: center;
  margin: 0;
}

.timer {
  color: #1f6e72;
  font-size: 150px;
  margin-bottom: 10px;
}

.buttons button {
  outline: none;
  border: none;
  font-size: 24px;
  background: transparent;
  color:  #1f6e72;
  border-radius: 5px;
  box-shadow: 0 0 5px #1f6e72;
  padding: 15px 30px;
  cursor: pointer;
  transition: 0.3s ease all;
}


.buttons button:hover {
  border-radius: 10px;
  background: #1f6e72;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
  color : #151618;
}

.buttons button:not(:last-child) {
  margin-right: 25px;
}


.task {

  outline: none;
  border: none;
  font-size: 24px;
  padding: 5px 10px;
}


.task input{

  background: transparent;
  color:  #49b2b8;;
  border: none;
  font-size: 35px;
  text-align: center;
}

.task input:focus {
  outline: none !important;
  border:1px solid #1f6e72;
  box-shadow: 0 0 10px#1f6e72
}

@media (max-width: 780px) {
  h2 {
    font-size: 22px;
  }

  .timer {
    font-size: 80px;
  }

  .buttons button {
    font-size: 18px;
  }
}

